<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <div v-for="(item,index) in fbxx" :key="index">
        <div class="content">
          <div class="common-main box">
            <div class="page-list">
              <div class="pagelist-left">包名称</div>
              <div class="pagelist-right">{{ item.bmc }}</div>
            </div>
            <div class="page-list">
              <div class="pagelist-left">包描述</div>
              <div class="pagelist-right">{{ item.bms }}</div>
            </div>
            <div class="page-list">
              <div class="pagelist-left">报名截止时间</div>
              <div class="pagelist-right">{{ getBmjzsj(item.bmjzsj) }}</div>
            </div>
            <div class="page-list">
              <div class="pagelist-left">开标时间</div>
              <div class="pagelist-right">{{ getKbsj(item.kbsj) }}</div>
            </div>
            <div class="page-list">
              <div class="pagelist-left">开标地点</div>
              <div class="pagelist-right">{{ item.kbdd }}</div>
            </div>
          </div>
        </div>
      </div>

    </div>
  </div>

</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar'
import { formatTimes } from '@/common/js/util'

export default {
  name: 'One',
  components: {
    headerBar
  },
  data() {
    return {
      fbxx: '',
      showMore: false,
      headerData: {
        title: '分包详情',
        leftUrl: 'back',
        leftText: ' '
      }
    }
  },
  computed: {

  },
  created() {
    this.xmid = this.$route.params.xmid
    this.getCqxx()
  },
  methods: {
    getBmjzsj(bmjzsj) {
      var time = formatTimes(bmjzsj)
      return time
    },
    getKbsj(kbsj) {
      var time = formatTimes(kbsj)
      return time
    },
    getCqxx() {
      this.$store.dispatch('gyswdxm/getGysFbxqAction', { xmid: this.xmid }).then(res => {
        console.log('res=======', res)
        this.fbxx = res
      })
    },
    MoreAction(item) {
      console.log('MoreAction======', item)
      const more = []
      more.push(item.showmore)
      more.push(item.title)
      this.$router.push({
        name: 'DbsxSjbqMore',
        params: { morexx: more }
      })
    }
  }

}
</script>
<style lang="css" scoped>

</style>
